.App {
  color: #000;
  color: initial;
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid #333;
  /* box-shadow: 0px 0px 15px gray; */
}
.Header, .Footer {
  width: 100%;
  background-color: #66d8f5;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Header h1 {
  font-size: 1.5rem;
}
.Header svg {
  font-size: 2rem;
}
.Footer {
  padding: 0.75rem;
  display: grid;
  place-content: center;
}
.Nav {
  width: 100%;
  background-color: #333;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.searchForm {
  width: 80%;
  padding: 1rem 0 0 0.75rem;
}
.searchForm input[type="text"] {
  font-family: 'Open Sans', sans-serif;
  width: 100%;
  min-height: 48px;
  font-size: 1rem;
  padding: 0.25rem;
  border-radius: 0.25rem;
  outline: none;
}
.searchForm label {
  position: absolute;
  left: -99999px;
}
.Nav ul {
  color: #fff;
  list-style-type: none;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
}
.Nav li {
  padding: 1rem;
}
.Nav li:hover, 
.Nav li:focus {
  padding: 1rem;
}
.Nav li a {
  color: #fff;
  text-decoration: none;
}
.Nav li:hover, 
.Nav li:focus, 
.Nav li:hover a, 
.Nav li:focus a  {
  background-color: #eee;
  color: #333;
}
.Nav li:hover a, 
.Nav li:focus a {
  cursor: pointer;
}
.Home, .NewPost, .PostPage, .About, .Missing {
  width: 100%;
  flex-grow: 1;
  padding: 1rem;
  overflow-y: auto;
  background-color: #fff;
}
.post {
  margin-top: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid lightgray;
}
.Home .post a {
  text-decoration: none;
}
.Home .post a, 
.Home .post a:visited {
  color: #000;
}
.post:first-child {
  margin-top: 0;
}
.post:last-child {
  border-bottom: none;
}
.postDate {
  font-size: 0.75rem;
  margin-top: 0.25rem;
}
.postBody {
  margin: 1rem 0;
}
.newPostForm {
  display: flex;
  flex-direction: column;
}
.newPostForm label {
  margin-top: 1rem;
}
.newPostForm input[type='text'], 
.newPostForm textarea {
  font-family: 'Open Sans', sans-serif;
  width: 100%;
  min-height: 48px;
  font-size: 1rem;
  padding: 0.25rem;
  border-radius: 0.25rem;
  margin-right: 0.25rem;
  outline: none;
}
.newPostForm textarea {
  height: 100px;
}
.newPostForm button {
  margin-top: 1rem;
  height: 48px;
  min-width: 48px;
  border-radius: 10px;
  padding: 0.5rem;
  font-size: 1rem;
  cursor: pointer;
}
.Missing p, .PostPage p, .NewPost p {
  margin-top: 1rem;
}
.PostPage button {
  height: 48px;
  min-width: 48px;
  border-radius: 0.25rem;
  padding: 0.5rem;
  font-size: 1rem;
  color: #fff;
  cursor: pointer;
}
.deleteButton {
  background-color: red;
}
.editButton {
  background-color: #333;
}
.statusMsg {
  margin-top: 2rem;
}
@media only screen and (min-width: 610px) {
  .Nav {
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
  }
  .Nav ul {
    text-align: right;
  }
  .Nav li:hover, 
  .Nav li:focus, 
  .Nav li:hover a, 
  .Nav li:focus a  {
    background-color: #eee;
    color: #333;
  }
  .searchForm {
    width: 50%;
    padding: .5rem 0;
  } 
  .searchForm input[type="text"] {
    margin-left: 0.5rem;
  }
  .newPostForm textarea {
    height: 300px;
  }
}
@media only screen and (min-width: 992px) {
  .Header svg {
    font-size: 3rem;
  }
}
